import React from 'react'
import { useMedia } from 'react-use'
import { useWeb3React } from '@web3-react/core'
import { Button, ButtonProps, ConnectorId, useWalletModal } from '@robustswap-libs/uikit'
import { injected, walletconnect } from 'connectors'
import useI18n from 'hooks/useI18n'
import { margin } from 'polished'

const UnlockButton: React.FC<ButtonProps> = props => {
  const TranslateString = useI18n()
  const { account, activate, deactivate } = useWeb3React()


  const handleLogin = (connectorId: ConnectorId) => {
    if (connectorId === 'walletconnect') {
      return activate(walletconnect)
    }
    return activate(injected)
  }

  const isMobile = useMedia('(max-width: 760px)')
  const panel = document.getElementsByClassName("sc-ehCJOs")[0]
  const marginLeft = isMobile ? 0 : panel.clientWidth;

  console.log("horrible:", marginLeft)
  const { onPresentConnectModal } = useWalletModal(handleLogin, `${marginLeft}px`, deactivate, account as string)

  return (
    <Button onClick={onPresentConnectModal} {...props}>
      {TranslateString(292, 'Unlock Wallet')}
    </Button>
  )
}

export default UnlockButton
